.section {
    display: flex;
  
}


.section-left{
    background-color: red;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.section-right{
    padding: 0px 40px;
    flex: 1;
    position: relative;
}


.section-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.head{
    padding-left: 20px;
    /* color: rgb(85, 85, 85);
    margin-bottom: 20px;
    margin-left:30px;
    letter-spacing: 5px;
    font-size: 11px; */
    font-weight: 400;
    padding-bottom: 20px;
}


.h2-c{
    padding-left: 20px;
    font-weight: 300;
}




@media screen and (max-width: 480px){
    .section {
        flex-direction: column;
        padding-top: 15px;
    }

    .section-left-wrapper {
        padding: 10px;
        height: 100%;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .h2-c{
       font-size: 18px;
    }

