.p {
  width: 20%;
  height: 84vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px;
  overflow: hidden;
  position:relative;
    z-index: 1;
}

.p-browser {
  height: 20px;
  background-color: rgb(161, 138, 138);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: rgb(241, 222, 222);
}

.p-img {
  width: 100%;
 
  z-index: 1;
  transition: 1s ease;
  
}


.p:hover .p-img {
  
  transition: 1s ease;
  transform: scale(1.2);
  opacity: 0.9;

  z-index: 0;
}


.portfolioList {
  font-size: 14px;
  margin-right: 50px;
  padding: 7px;
  border-radius: 10px;
  cursor: pointer;
  
}


a.project__btn {
  text-decoration: none;
  top: -50%;
  left: 0px;
  padding: 12px 35px;
  border: 2px solid #ffffff87;
  background-color: #0a8a06;
  opacity: 0.7;
  color: white;
  border-radius: 5px;
  transition: .4s ease;
  position:relative;
  z-index: 999;


}
a.project__btn:hover{
  color: white;
  opacity: 1;
  background-color:  #59b256;
  border: 2px solid  #59b256;
}

